import { getWordsData } from '@/api/words'

export default {
  name: 'Content',
  data () {
    return {
      articleData: {}
    }
  },
  created () {
    this.getWordsData()
  },
  methods: {
    getWordsData () {
      getWordsData(this.$route.params.id).then(res => {
        this.articleData = res.data.data
      })
    }
  }
}
